@import "../mixins";

header {
    display: block;
    width: 100%;
    height: 60px;
    background: #fff;
    box-shadow: 0 0 10px rgba(0,0,0,.1);
    padding: 10px 0;
    position: fixed;
    z-index: 50;
    top: 0;
    left: 0;
    font-size: 0;

    @include mq(600px){
        height: 54px;
        padding: 7px 20px;
    }
    
    section {
        display: block;
        width: 90%;
        max-width: 1000px;
        margin: 0 auto;
        position: relative;
        padding: 0 40px 0 40%;

        @include mq(800px){
            padding-left: 30%;
        }

        @include mq(600px){
            width: 100%;
            padding-left: 40px;
        }
        
        .title {
            display: inline-block;
            vertical-align: middle;
            height: 40px;
            @include S-Regular(40px, 25px, #444, left);
            font-weight: bolder;
            letter-spacing: 1px;
            position: absolute;
            z-index: 2;
            top: 0;
            left: 0;
            
            span {
                font-size: 70%;
            }

            &::after {
                content: "Beta";
                display: inline-block;
                position: absolute;
                top: 0;
                left: 100%;
                line-height: 20px;
                font-size: 12px;
                color: #999;
                letter-spacing: 0;
                text-transform: none;
            }

            .logo {
                display: none;
            }

            @include mq(600px){
                font-size: 0;
                letter-spacing: 0;

                &::after {
                    display: none;
                }

                .logo {
                    display: block;
                    height: 40px;
                    padding: 3px 6px 3px 0;
                }
            }
        }
        
        @import "./HeaderSearch";

        .menu {
            display: inline-block;
            vertical-align: middle;
            width: 20px;
            height: 40px;
            overflow: visible;
            position: absolute;
            z-index: 2;
            top: 0;
            right: 0;
            outline: 0;

            .hamburger {
                display: block;
                width: 100%;
                height: 100%;
                border: 0;
                outline: 0;
                background: transparent;

                span {
                    display: block;
                    width: 100%;
                    height: 2px;
                    background: #444;
                    margin: 5px auto;
                    border-radius: 2px;
                    transform-origin: left;
                    transition: .2s linear;
                }

                &.menu-visible span {
                    &:nth-child(1){
                        transform: rotate(45deg);
                    }

                    &:nth-child(2){
                        opacity: 0;
                    }

                    &:nth-child(3){
                        transform: rotate(-45deg);
                    }
                }
            }

            &:focus .hamburger {
                background: #eee;
            }

            ul {
                display: none;
                width: 200px;
                background: #444;
                border-radius: 5px;
                position: absolute;
                z-index: 10;
                top: 50px;
                right: 10px;

                @supports (transform: scale(0)){
                    display: block;
                    transform: scale(0);
                    transform-origin: right top;
                    transition: .1s linear;
                }
                
                .triangle {
                    display: block;
                    width: 20px;
                    height: 20px;
                    position: absolute;
                    z-index: 2;
                    top: -15px;
                    right: 0;

                    polyline {
                        stroke: 0;
                        fill: #444;
                    }
                }

                &.visible {
                    display: block;
                    
                    @supports (transform: scale(1)){
                        transform: scale(1);
                    }
                }

                li {
                    display: block;
                    width: 100%;
                    position: relative;
                    z-index: 2;
                    
                    a {
                        display: block;
                        width: 100%;
                        @include SS-Regular(40px, 12px, #fff, center);
                        padding: 0 10px;
                        text-transform: uppercase;
                        outline: 0;

                        &.active {
                            display: none;
                        }
                        
                        &:hover,
                        &:focus {
                            background: #333;
                        }
                    }

                    &:first-of-type a {
                        padding-top: 10px;
                        border-radius: 5px 0 0 0;
                    }

                    &:last-of-type a {
                        padding-bottom: 10px;
                        border-radius: 0 0 5px 5px;
                    }
                }
            }
        }
    }
}