@import "../mixins";

footer {
    display: block;
    width: 100%;
    padding: 25px 5%;
    font-size: 0;
    text-align: left;
    border-top: 1px solid #eee;

    @include mq(600px){
        padding: 25px 20px;
    }

    p {
        display: inline-block;
        vertical-align: middle;
        @include SS-Regular(20px, 15px, #777, left);

        br {
            display: none;

            @include mq(600px){
                display: block;
            }
        }

        a {
            color: #8a2be2;
            text-decoration: underline;
        }

        @include mq(800px){
            display: block;
            width: 100%;
            margin-bottom: 25px;
        }
    }

    .nav {
        display: inline-block;
        vertical-align: middle;
        @include SS-Regular(20px, 15px, #8a2be2, left);
        margin-left: 20px;

        @include mq(800px){
            margin-left: 0;
            margin-right: 20px;

            &:last-of-type {
                margin-right: 0;
            }
        }

        @include mq(400px){
            min-width: 60%;
            margin-right: 0;
            margin-bottom: 25px;

            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }
}