@import "../../mixins";

.results {
    display: block;
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;

    .bg-layer {
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        background: rgba(0,0,0,.9);
    }

    section {
        display: block;
        width: 90%;
        max-width: 400px;
        height: 80%;
        overflow: hidden;
        position: relative;
        z-index: 10;
        top: 10%;
        margin: 0 auto;
        background: #fff;
        border-radius: 10px;
        box-shadow: 0 0 10px rgba(0,0,0,.3);
        transform: scale(0);
        transform-origin: center;
        transition: .15s linear;
        
        &.visible {
            transform: scale(1);
        }

        @include mq(600px){
            width: 100%;
            max-width: 100%;
            height: 100%;
            top: 100%;
            border-radius: 0;
            transform: scale(1);
            transform-origin: bottom;

            &.visible {
                top: 0;
            }
        }

        .header {
            display: block;
            width: 100%;
            position: absolute;
            z-index: 9;
            top: 0;
            left: 0;
            background: #fff;
            border-radius: 10px 10px 0 0;
            padding: 4px;

            .left-arrow {
                display: block;
                width: 50px;
                height: 50px;
                border: 0;
                outline: 0;
                background: transparent;
                padding: 16px;
                position: absolute;
                z-index: 2;
                top: 4px;
                left: 0;
                
                svg {
                    display: block;
                    width: 100%;
                    height: 100%;
    
                    polyline {
                        stroke: #777;
                        stroke-width: 5;
                        stroke-linecap: round;
                        stroke-linejoin: round;
                        fill: none;
                    }
                }
            }

            @import "./ResultsSearch";
        }

        .message {
            display: block;
            width: 90%;
            position: absolute;
            z-index: 1;
            top: 50%;
            left: 5%;
            margin-top: -35px;

            img {
                display: block;
                width: 40px;
                height: 40px;
                margin: 0 auto 10px auto;
            }

            span {
                display: block;
                width: 100%;
                @include SS-Regular(20px, 15px, #444, center);
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        .list {
            display: block;
            width: 100%;
            height: 100%;
            overflow: hidden;
            position: relative;
            z-index: 1;
            top: 58px;
            padding-bottom: 58px;
            
            .songs {
                display: block;
                width: 100%;
                height: 100%;
                overflow: auto;
                padding: 15px 20px 30px 20px;
                border-top: 1px solid #eee;

                .results-count {
                    display: block;
                    width: 100%;
                    @include SS-Regular(20px, 15px, #444, left);
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    background: #fff;
                    margin-bottom: 30px;
                }

                @import "./ResultsSong";
            }
        }
    }
}