@import "../mixins";

.search {
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    height: 40px;
    position: relative;
    z-index: 1;
    background: #fff;

    input {
        display: block;
        width: 100%;
        height: 40px;
        padding: 0 40px 0 15px;
        border: 1px solid #ccc;
        border-radius: 5px;
        outline: 0;
        background: transparent;
        position: relative;
        z-index: 1;
        @include SS-Regular(40px, 15px, #444, left);

        &::-webkit-calendar-picker-indicator {
            display: none;
        }
        
        &:focus {
            border-color: #aaa;
        }

        @include mq(600px){
            background: #eee;
            border: 0;
        }
    }

    button {
        display: block;
        width: 40px;
        height: 40px;
        position: absolute;
        z-index: 2;
        top: 0;
        right: 0;
        border: 0;
        outline: 0;
        background: transparent;
        border-radius: 0 5px 5px 0;
        padding: 12px;

        svg {
            display: block;
            width: 100%;
            height: 100%;

            path {
                stroke: #777;
            }
        }
    }
}