@import "../mixins";

form {
    display: block;
    width: 100%;

    .prev {
        display: inline-block;
        vertical-align: middle;
        height: 35px;
        padding: 0 30px;
        border-radius: 4px;
        outline: 0;
        background: #ddd;
        @include SS-Regular(35px, 14px, #444, center);
        margin-bottom: 30px;
        cursor: default;

        &:focus {
            background: #ccc;
        }

        svg {
            display: inline-block;
            vertical-align: middle;
            width: 15px;
            height: 15px;
            margin-right: 10px;

            polyline {
                stroke: #444;
                stroke-width: 4;
                stroke-linejoin: round;
                stroke-linecap: round;
                fill: none;
            }
        }
    }
    
    label {
        display: inline-block;
        max-width: 100%;
        @include SS-Regular(20px, 16px, #777, left);
        font-weight: bolder;
        margin-bottom: 7px;

        span {
            font-weight: normal;
            font-size: 80%;
        }

        @include mq(600px){
            font-size: 15px;
        }
    }

    .thumbnail {
        display: block;
        width: 0;
        height: 0;
        overflow: visible;
        position: relative;
        z-index: 2;
        top: 0;
        left: 0;
        transform-origin: left;
        transition: .2s linear;

        &.visible {
            width: 80px;
        }

        div {
            display: block;
            width: 100%;
            height: 50px;
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            border-radius: 4px 0 0 4px;
        }
    }

    input[type=text] {
        display: block;
        width: 100%;
        height: 50px;
        border: 1px solid #ccc;
        outline: 0;
        background: transparent;
        border-radius: 4px;
        @include SS-Regular(50px, 16px, #444, left);
        padding: 0 15px;
        margin-bottom: 30px;
        position: relative;
        z-index: 1;
        transition: .2s linear;

        &::-webkit-calendar-picker-indicator {
            display: none;
        }

        &:focus {
            border-color: #777;
        }

        &.with-thumbnail {
            padding-left: 90px;
        }

        @include mq(600px){
            font-size: 15px;
        }
    }

    #lyrics {
        display: block;
        width: 100%;
        height: auto;
        min-height: 230px;
        border: 1px solid #ccc;
        outline: 0;
        padding: 15px;
        border-radius: 4px;
        background: transparent;
        @include SS-Regular(20px, 16px, #444, left);
        margin-bottom: 30px;
        transition: .2s linear;

        &:focus {
            border-color: #777;
        }

        @include mq(600px){
            font-size: 15px;
        }
    }

    .options {
        display: block;
        width: 100%;
        height: 50px;
        overflow: visible;
        margin-bottom: 30px;
        position: relative;
        z-index: 1;

        svg {
            display: block;
            width: 16px;
            height: 16px;
            position: absolute;
            z-index: 1;
            top: 17px;
            right: 17px;

            polyline {
                stroke: #444;
                stroke-width: 4;
                fill: none;
                stroke-linecap: round;
            }
        }

        select {
            display: block;
            width: 100%;
            height: 50px;
            border: 1px solid #ccc;
            outline: 0;
            background: transparent;
            border-radius: 4px;
            @include SS-Regular(50px, 16px, #444, left);
            padding: 0 50px 0 15px;
            position: relative;
            z-index: 2;
            appearance: none;
            transition: .2s linear;

            &:focus {
                border-color: #777;
            }
        }
    }

    .message {
        display: block;
        width: 100%;
        @include SS-Regular(20px, 16px, #cd5c5c, left);
        font-weight: bolder;
        position: relative;
        top: -23px;

        &.unexpected {
            top: 7px;
        }

        @include mq(600px){
            font-size: 15px;
        }
    }

    button {
        display: block;
        width: 100%;
        height: 50px;
        border: 0;
        outline: 0;
        border-radius: 4px;
        @include SS-Regular(50px, 16px, #fff, center);
        font-weight: bolder;

        @include mq(600px){
            font-size: 15px;
        }

        &[type=submit]{
            background: #8a2be2;
        }

        &[type=button]:not(.prev){
            background: #87ceeb;

            img {
                display: block;
                width: 20px;
                margin: 0 auto;
            }
        }
    }
}