@import "../mixins";

.search {
    display: block;
    width: 100%;
    height: 50px;
    margin: 20px auto 0 auto;
    position: relative;
    background: #fff;

    input {
        display: block;
        width: 100%;
        height: 50px;
        padding: 0 65px 0 15px;
        border: 1px solid #ccc;
        border-radius: 5px;
        outline: 0;
        background: transparent;
        position: relative;
        z-index: 1;
        @include SS-Regular(50px, 15px, #444, left);

        &::-webkit-calendar-picker-indicator {
            display: none;
        }
        
        &:focus {
            border-color: #777;
        }
    }

    button {
        display: block;
        width: 50px;
        height: 50px;
        position: absolute;
        z-index: 2;
        top: 0;
        right: 0;
        border: 0;
        outline: 0;
        background: #8a2be2;
        border-radius: 0 5px 5px 0;
        padding: 15px;

        svg {
            display: block;
            width: 100%;
            height: 100%;

            path {
                stroke: #fff;
            }
        }
    }
}