@import "../../mixins";

.song-may-exist {
    display: block;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 101;
    top: 0;
    left: 0;

    .bg-layer {
        display: block;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,.9);
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
    }

    section {
        display: block;
        width: 90%;
        max-width: 400px;
        margin: 0 auto;
        height: 80%;
        overflow: hidden;
        background: #000;
        border-radius: 10px;
        position: relative;
        z-index: 2;
        top: 10%;
        padding-bottom: 50px;
        box-shadow: 0 0 10px rgba(0,0,0,.3);
        transform: scale(0);
        transform-origin: center;
        transition: .15s linear;

        &.visible {
            transform: scale(1);
        }
        
        .sub-container {
            display: block;
            width: 100%;
            height: 100%;
            overflow: auto;
            position: relative;
            z-index: 1;

            .header {
                display: block;
                width: 100%;
                background-color: #b83f3f;
                background-repeat: no-repeat;
                background-position: center;
                background-size: cover;

                span {
                    display: block;
                    width: 100%;
                    padding: 20px;
                    @include SS-Regular(25px, 20px, #fff, left);
                    font-weight: bolder;

                    &.with-thumbnail {
                        padding: 100px 20px 20px 20px;
                        background: rgba(0,0,0,.7);
                        background: linear-gradient(to bottom, rgba(184,63,63,.3), rgba(184,63,63,1));

                        @include mq(400px){
                            padding-top: 80px;
                        }
                    }

                    @include mq(400px){
                        font-size: 18px;
                    }
                }
            }

            .song {
                display: block;
                width: 100%;
                padding: 20px 20px 30px 20px;
                background: #fff;

                span {
                    display: block;
                    width: 100%;
                    @include SS-Regular(20px, 16px, #444, left);
                    margin-bottom: 10px;

                    b {
                        color: #222;
                    }

                    a {
                        text-decoration: underline;
                        font-weight: bolder;
                        color: #8a2be2;
                    }

                    @include mq(400px){
                        font-size: 14px;
                    }
                }

                .lyrics {
                    display: block;
                    width: 100%;
                    @include SS-Regular(20px, 16px, #444, left);
                    padding-top: 20px;

                    b {
                        display: block;
                        width: 100%;
                        color: #222;
                        margin-bottom: 20px;
                    }

                    p {
                        display: block;
                        width: 100%;
                        min-height: 20px;
                    }

                    @include mq(400px){
                        font-size: 14px;
                    }
                }
            }
        }

        .options {
            display: block;
            width: 100%;
            height: 55px;
            overflow: hidden;
            position: absolute;
            z-index: 2;
            bottom: 0;
            left: 0;
            background: #fff;
            box-shadow: 0 -20px 20px #fff;
            border-top: 5px solid #fff;

            button {
                display: inline-block;
                vertical-align: middle;
                width: 50%;
                @include SS-Regular(50px, 15px, #444, center);
                font-weight: bolder;
                border: 0;
                outline: 0;
                background: #ddd;

                &:focus {
                    background: #ccc;
                }

                &:last-of-type {
                    border-left: 1px solid #fff;
                }

                @include mq(400px){
                    font-size: 14px;
                }
            }
        }
    }
}