@import "../../mixins";

.song-exist {
    display: block;
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: fixed;
    z-index: 102;
    top: 0;
    left: 0;

    .bg-layer {
        display: block;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,.9);
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
    }

    section {
        display: block;
        width: 90%;
        max-width: 400px;
        height: 80%;
        overflow: hidden;
        background: #000;
        border-radius: 10px;
        position: relative;
        z-index: 2;
        top: 10%;
        margin: 0 auto;
        box-shadow: 0 0 10px rgba(0,0,0,.3);
        padding-bottom: 50px;
        transform: scale(0);
        transform-origin: center;
        transition: .15s linear;

        &.visible {
            transform: scale(1);
        }
        
        .sub-container {
            display: block;
            width: 100%;
            height: 100%;
            overflow: auto;
            position: relative;
            z-index: 2;

            .left-arrow {
                display: block;
                width: 25px;
                height: 25px;
                position: absolute;
                z-index: 3;
                top: 20px;
                left: 20px;
                border: 0;
                outline: 0;
                background: transparent;
                padding: 3px;
                
                svg {
                    display: block;
                    width: 100%;
                    height: 100%;

                    polyline {
                        stroke: #fff;
                        stroke-width: 4;
                        fill: none;
                        stroke-linejoin: round;
                        stroke-linecap: round;
                    }
                }

                @include mq(400px){
                    width: 20px;
                    height: 20px;
                    padding: 0;
                }
            }

            .header {
                display: block;
                width: 100%;
                padding: 65px 20px 20px 20px;
                position: relative;
                z-index: 1;
                background-repeat: no-repeat;
                background-position: center;
                background-size: cover;
                background-color: #000;

                &::after {
                    content: "";
                    display: block;
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    z-index: 1;
                    top: 0;
                    left: 0;
                    background: rgba(0,0,0,.7);
                    background: linear-gradient(to bottom, rgba(0,0,0,.7), #000);
                }

                @include mq(400px){
                    padding-top: 60px;
                }

                &.with-thumbnail {
                    padding-top: 100px;

                    @include mq(400px){
                        padding-top: 80px;
                    }
                }

                .title {
                    display: block;
                    width: 100%;
                    @include SS-Regular(25px, 20px, #fff, left);
                    font-weight: bolder;
                    position: relative;
                    z-index: 2;

                    @include mq(400px){
                        line-height: 22px;
                        font-size: 18px;
                    }
                }

                .message {
                    display: block;
                    width: 100%;
                    @include SS-Regular(20px, 14px, #bbb, left);
                    position: relative;
                    z-index: 2;

                    b {
                        color: #ddd;
                    }

                    @include mq(400px){
                        font-size: 14px;
                    }
                }
            }

            .lyrics {
                display: block;
                width: 100%;
                padding: 20px 20px 30px 20px;
                position: relative;
                z-index: 1;
                background: #fff;

                p {
                    display: block;
                    width: 100%;
                    min-height: 20px;
                    @include S-Regular(20px, 16px, #444, left);

                    @include mq(400px){
                        font-size: 14px;
                    }
                }
            }
        }

        .options {
            display: block;
            width: 100%;
            height: 55px;
            overflow: hidden;
            position: absolute;
            z-index: 3;
            bottom: 0;
            left: 0;
            background: #fff;
            font-size: 0;
            box-shadow: 0 -20px 20px #fff;
            border-top: 5px solid #fff;

            a, button {
                display: inline-block;
                vertical-align: middle;
                width: 50%;
                @include SS-Regular(50px, 15px, #444, center);
                font-weight: bolder;
                border: 0;
                outline: 0;
                background: #ddd;

                &:focus {
                    background: #ccc;
                }

                @include mq(400px){
                    font-size: 14px;
                }
            }
            
            a {
                border-left: 1px solid #fff;
            }
        }
    }
}