@import "../mixins";

.home {
    display: block;
    width: 100%;
    background: #fff;

    &.fixed {
        height: 100%;
        position: fixed;
        z-index: 1;
        top: 0;
        left: 0;
        overflow: hidden;
    }

    .intro {
        display: block;
        width: 90%;
        max-width: 500px;
        margin: 0 auto 70px auto;
        text-align: center;

        @include mq(600px){
            width: 100%;
            max-width: 600px;
            padding: 0 20px;
            margin: 0 auto 35px auto;
        }

        section {
            display: block;
            width: 100%;
            height: 310px;
            overflow: visible;
            transform: scale(1);
            transform-origin: center;
            transition: .15s ease-in-out;
            
            @include mq(1000px){
                height: 275px;
            }

            @include mq(400px){
                height: 260px;
            }
            
            &.hidden {
                height: 0;
                overflow: hidden;
                transform: scale(0);
            }

            h1 {
                display: inline-block;
                vertical-align: middle;
                @include S-Regular(auto, 50px, #444, center);
                letter-spacing: 3px;
                position: relative;
                margin-top: 70px;

                span {
                    font-size: 70%;
                }

                &::after {
                    content: "Beta";
                    display: inline-block;
                    position: absolute;
                    top: 0;
                    left: 100%;
                    line-height: 20px;
                    font-size: 16px;
                    color: #999;
                    letter-spacing: 0;
                    text-transform: none;
                }

                @include mq(1000px){
                    font-size: 40px;
                }

                @include mq(400px){
                    font-size: 35px;
                    letter-spacing: 2px;
                }
            }

            p {
                display: block;
                width: 100%;
                @include S-Regular(20px, 15px, #444, center);
                font-weight: normal;

                @include mq(1000px){
                    font-size: 14px;
                }

                @include mq(400px){
                    font-size: 12px;
                }
            }

            .music_notes {
                display: block;
                width: 300px;
                margin: 20px auto 0 auto;
                position: relative;

                svg {
                    display: block;
                    width: 100%;
                    filter: drop-shadow(0 30px 5px rgba(0,0,0,.1));

                    path {
                        fill: #444;
                    }
                }

                @include mq(1000px){
                    width: 260px;
                }

                @include mq(400px){
                    width: 230px;
                }
            }
        }

        @import "../partials/IntroSearch";

        .add-btn {
            display: block;
            width: 50%;
            margin: 30px auto 0 auto;
            border: 1px solid #ccc;
            border-radius: 5px;
            outline: 0;
            @include SS-Regular(40px, 15px, #8a2be2, center);
            position: relative;

            &:focus {
                border-color: #777;
            }

            &::before,
            &::after {
                content: "";
                display: block;
                width: 40%;
                border-bottom: 1px dashed #ccc;
                position: absolute;
                top: 19px;
            }

            &::before {
                left: -50%;
            }

            &::after {
                right: -50%;
            }

            @include mq(600px){
                width: 100%;
                margin-top: 10px;

                &::before,
                &::after {
                    display: none;
                }
            }
        }
    }

    .categories {
        display: block;
        width: 100%;
        padding: 50px 0;
        border-top: 1px solid #eee;

        @include mq(600px){
            padding: 25px 0 50px 0;
        }

        .header {
            display: block;
            width: 90%;
            max-width: 900px;
            margin: 0 auto;
            font-size: 0;
            text-align: left;

            @include mq(600px){
                width: 100%;
                padding: 0 20px;
            }

            h2 {
                display: inline-block;
                vertical-align: middle;
                @include S-Regular(30px, 25px, #444, left);

                @include mq(1000px){
                    font-size: 22px;
                }
            }

            nav {
                display: inline-block;
                vertical-align: middle;
                float: right;

                .nav {
                    display: inline-block;
                    vertical-align: middle;
                    margin-right: 20px;
                    @include SS-Regular(30px, 15px, #8a2be2, left);

                    &:last-of-type {
                        margin-right: 0;
                    }

                    &:hover {
                        text-decoration: underline;
                    }

                    @include mq(400px){
                        min-width: 60%;
                        max-width: 100%;
                        margin: 0 0 10px 0;

                        &:last-of-type {
                            margin: 0;
                        }
                    }
                }

                @include mq(600px){
                    display: block;
                    float: none;
                    text-align: left;
                    margin-top: 25px;
                }
            }
        }

        .songs {
            display: block;
            width: 90%;
            max-width: 900px;
            margin: 50px auto 0 auto;
            font-size: 0;

            @include mq(600px){
                width: 100%;
                padding: 0 20px;
                margin: 25px auto 0 auto;
            }

            h2 {
                display: block;
                width: 100%;
                @include SS-Regular(50px, 20px, #444, left);
                border-bottom: 1px solid #ddd;
                margin-bottom: 50px;
            }

            @import "../partials/Song";
        }
    }
}