@font-face {
    font-family: SS-Regular;
    src: url(../fonts/Poppins-Regular.ttf);
}

@font-face {
    font-family: S-Regular;
    src: url(../fonts/Georgia-Regular.ttf);
}

@mixin SS-Regular($lineHeight, $fontSize, $color, $textAlign) {
    line-height: $lineHeight;
    font-size: $fontSize;
    color: $color;
    text-align: $textAlign;
    font-family: sans-serif;
}

@mixin S-Regular($lineHeight, $fontSize, $color, $textAlign) {
    line-height: $lineHeight;
    font-size: $fontSize;
    color: $color;
    text-align: $textAlign;
    font-family: S-Regular, serif;
}

@mixin mq($width){
    @media screen and (max-width: $width) {
        @content;
    }
}