@import "../mixins";

.add {
    display: block;
    width: 100%;

    main {
        display: block;
        width: 100%;
        min-height: calc(100vh - 60px);
        padding-top: 60px;

        &.fixed {
            height: 100%;
            min-height: 100%;
            overflow: hidden;
            position: fixed;
            z-index: 1;
            top: 0;
            left: 0;
        }

        @include mq(600px){
            padding-top: 54px;
        }

        .sub-container {
            display: block;
            width: 90%;
            max-width: 600px;
            margin: 0 auto;
            padding: 70px 0;

            @include mq(600px){
                width: 100%;
                padding: 50px 20px;
            }

            h1 {
                display: block;
                width: 100%;
                @include S-Regular(35px, 30px, #444, left);
                margin-bottom: 50px;
                padding-bottom: 20px;
                position: relative;

                &::after {
                    content: "";
                    display: block;
                    width: 50px;
                    height: 4px;
                    background: #cd5c5c;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                }

                @include mq(600px){
                    line-height: 30px;
                    font-size: 25px;
                }
            }
            
            @import "../partials/Form";
        }
    }

    @import "../partials/modals/SongExist";
}