@import "../../mixins";

.search {
    display: block;
    width: 100%;
    position: relative;
    background: #fff;

    input {
        display: block;
        width: 100%;
        height: 50px;
        padding: 0 50px;
        border: 0;
        border-radius: 5px;
        outline: 0;
        background: #eee;
        position: relative;
        z-index: 1;
        @include SS-Regular(40px, 15px, #444, left);

        &::-webkit-calendar-picker-indicator {
            display: none;
        }
    }
    
    button {
        display: block;
        width: 50px;
        height: 50px;
        position: absolute;
        z-index: 2;
        top: 0;
        right: 0;
        border: 0;
        outline: 0;
        background: transparent;
        padding: 15px;

        svg {
            display: block;
            width: 100%;
            height: 100%;

            path {
                stroke: #777;
            }
        }
    }
}