@import "../../mixins";

.song {
    display: block;
    width: 100%;
    margin-bottom: 30px;

    &:last-of-type {
        margin-bottom: 0;
    }
    
    .head {
        display: block;
        width: 100%;
        @include SS-Regular(20px, 13px, #444, left);
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        a {
            color: #444;

            &:hover {
                text-decoration: underline;
            }
        }

        span {
            display: inline-block;
            vertical-align: middle;
            width: 4px;
            height: 4px;
            background: #777;
            border-radius: 50%;
            margin: 0 5px;
        }
    }
    
    .title {
        display: inline-block;
        max-width: 100%;
        @include SS-Regular(25px, 20px, #8a2be2, left);
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        &:hover {
            text-decoration: underline;
        }
    }

    .content {
        display: block;
        width: 100%;
        min-height: 70px;
        position: relative;
        margin-top: 5px;

        &.with-thumbnail {
            padding-left: 130px;
            margin-top: 10px;
        }

        .thumbnail {
            display: block;
            width: 120px;
            height: 70px;
            position: absolute;
            top: 0;
            left: 0;
            border-radius: 4px;
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;

            svg {
                display: block;
                width: 20px;
                height: 20px;
                position: absolute;
                top: 50%;
                left: 50%;
                margin-top: -10px;
                margin-left: -10px;

                circle {
                    stroke: none;
                    fill: rgba(255,255,255,.7);
                }

                polyline {
                    stroke: none;
                    fill: rgba(0,0,0,.3);
                    stroke-linejoin: round;
                }
            }
        }

        .lyrics {
            display: block;
            max-width: 100%;
            max-height: 60px;
            overflow: hidden;
            @include SS-Regular(20px, 15px, #777, left);
        }

        .ellipsis::after {
            content: "...";
            display: inline-block;
            height: 10px;
            @include SS-Regular(10px, 15px, #777, left);
        }
    }
}